import React, { useState, useEffect } from 'react'
import classNames from 'classnames/bind'
import { useHistory } from 'react-router'
import Partners from '@/components/header/Partners'
import { useWebpImg } from '@/utils/util'
import scss from './error.module.scss'

const cx = classNames.bind(scss)

export default function Error404() {
  const history = useHistory()
  const [time, setTime] = useState(10)

  useEffect(countdown, [time])

  function toHome() {
    history.replace('/home')
  }

  function countdown() {
    let timer
    if (time <= 0) {
      toHome()
    } else {
      timer = setTimeout(() => setTime(time - 1), 1000)
    }
    return () => clearTimeout(timer)
  }

  return (
    <div className={cx('error')}>
      <div className={cx('error__wrapper')}>
        <div className={cx('error__header')}>
          <a onClick={toHome} className={cx('error__logo')} href="javascript:void (0);"/>
          <span className={cx('error__vertical-line')}/>
          <Partners/>
        </div>
        <div className={cx('error__content')}>
          <img className={cx('error__img')} src={require('@/views/common/img/404.jpg' + useWebpImg())} alt="404"/>
          <div className={cx('error__tips-box', 'clearfix')}>
            <h3 className={cx('error__title')}>
              当前页面无法连接...
            </h3>
            <p className={cx('error__tips')}>
              很抱歉，您所尝试的页面因网络连接超时，暂时无法打开。若有任何疑问您可通过以下方式 联系我们的客服代表：
            </p>
            <p className={cx('error__email')}>
              电邮：<a className={cx('error__link')} href="mailto:k8casino@gmail.com">cs@k8.com</a>
            </p>
            <p className={cx('error__button-wrapper')}>
              <button className={cx('error__button')}>在线客服</button>
              <span>
                <span className={cx('monospaced-font')}>{time}</span>s&nbsp;后自动返回&nbsp;
              </span>
              <a onClick={toHome} className={cx('error__link')} href="javascript:void (0);">首页</a>
            </p>
            <p className={cx('error__horizontal-line')}/>
            <p className={cx('error__tips')}>
              Sorry,the page you requested can not open now.Please try again later. Should you have any inquiries,
              please contact our customer service centre
            </p>
            <p className={cx('error__email')}>
              email：<a className={cx('error__link')} href="mailto:k8casino@gmail.com">cs@k8.com</a>
            </p>
            <p className={cx('error__button-wrapper')}>
              <button className={cx('error__button')}>Live Chat</button>
              <span>
                <span className={cx('monospaced-font')}>{time}</span>&nbsp;second back to&nbsp;
              </span>
              <a onClick={toHome} className={cx('error__link')} href="javascript:void (0);">Home</a>
            </p>
          </div>
        </div>
      </div>
    </div>
  )
}
